import React, { useState } from 'react';
import { PlayIcon, CheckIcon, XMarkIcon } from '@heroicons/react/24/outline';
import { vendaFinalService } from '../../services/api';

interface GenerateFaturaButtonProps {
  vendaFinalId: number;
  status: string;
  id_payment?: string;
  onSuccess?: () => void;
  onError?: (error: string) => void;
}

export const GenerateFaturaButton: React.FC<GenerateFaturaButtonProps> = ({
  vendaFinalId,
  status,
  id_payment,
  onSuccess,
  onError
}) => {
  const [isLoading, setIsLoading] = useState(false);

  // Verificar se pode gerar: não tem id_payment OU status é CANCELLED
  const canGenerate = !id_payment || status === 'CANCELLED';

  const handleGenerate = async () => {
    if (!canGenerate) return;

    setIsLoading(true);
    try {
      const response = await vendaFinalService.generate(vendaFinalId);
      
      if (response.message) {
        onSuccess?.();
      }
    } catch (error: any) {
      const errorMessage = error.response?.data?.error || error.message || 'Erro ao gerar fatura';
      onError?.(errorMessage);
    } finally {
      setIsLoading(false);
    }
  };

  if (!canGenerate) {
    return (
      <button
        disabled
        className="flex items-center gap-2 px-3 py-2 text-sm text-gray-500 bg-gray-100 rounded-lg cursor-not-allowed"
        title="Esta fatura já foi gerada e está ativa"
      >
        <CheckIcon className="w-4 h-4" />
        Já Gerada
      </button>
    );
  }

  return (
    <button
      onClick={handleGenerate}
      disabled={isLoading}
      className={`flex items-center gap-2 px-3 py-2 text-sm rounded-lg transition-colors ${
        isLoading
          ? 'bg-blue-100 text-blue-600 cursor-not-allowed'
          : 'bg-blue-500 text-white hover:bg-blue-600'
      }`}
      title={status === 'CANCELLED' ? 'Regenerar fatura cancelada' : 'Gerar fatura no gateway'}
    >
      {isLoading ? (
        <>
          <div className="w-4 h-4 border-2 border-blue-600 border-t-transparent rounded-full animate-spin" />
          Gerando...
        </>
      ) : (
        <>
          <PlayIcon className="w-4 h-4" />
          {status === 'CANCELLED' ? 'Regenerar' : 'Gerar'}
        </>
      )}
    </button>
  );
};
