import React, { useState } from 'react';
import { VendaFinal } from '@/services/api/venda.service';
import { formatCurrency, formatDate } from '@/utils/format';
import { CheckCircleIcon, TimeIcon, AlertIcon, CopyIcon } from '@/icons';
import { GenerateFaturaButton } from './GenerateFaturaButton';
import toast from 'react-hot-toast';

interface FaturaCardProps {
  vendaFinal: VendaFinal;
  onCopyPix?: (copiacola: string) => void;
  onShowQRCode?: (copiacola: string, qrcode: string | undefined) => void;
  onGenerateSuccess?: () => void;
}

const getStatusIcon = (status: string) => {
  switch (status) {
    case 'PAGO':
    case 'APPROVED':
      return <CheckCircleIcon className="w-7 h-7 text-green-500" />;
    case 'PENDENTE':
    case 'PENDING':
      return <TimeIcon className="w-7 h-7 text-yellow-500" />;
    case 'CANCELLED':
      return <AlertIcon className="w-7 h-7 text-red-500" />;
    default:
      return <TimeIcon className="w-7 h-7 text-gray-500" />;
  }
};
  const statusTranslate = (status: string) => {
    switch (status) {
      case 'APPROVED':
        return 'Pago';
      case 'PENDING':
        return 'Pendente';
      case 'CANCELLED':
        return 'Cancelado';
      default:
        return status;
    }
  };

const getStatusColor = (status: string) => {
  switch (status) {
    case 'PAGO':
    case 'APPROVED':
      return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300';
    case 'PENDENTE':
    case 'PENDING':
      return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-300';
    case 'CANCELLED':
      return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-300';
    default:
      return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-300';
  }
};

const FaturaCard: React.FC<FaturaCardProps> = ({
  vendaFinal,
  onCopyPix,
  onShowQRCode,
  onGenerateSuccess
}) => {
  const [copied, setCopied] = useState(false);

  const copyToClipboard = async (text: string, type: string) => {
    try {
      await navigator.clipboard.writeText(text);
      setCopied(true);
      toast.success(`${type} copiado para a área de transferência!`);
      setTimeout(() => setCopied(false), 2000);
      onCopyPix?.(text);
    } catch (error) {
      toast.error('Erro ao copiar para a área de transferência');
    }
  };

  const showQRCode = () => {
    if (vendaFinal.qrcode) {
      onShowQRCode?.(vendaFinal.copiacola || "", vendaFinal.qrcode);
    } else if (vendaFinal.copiacola) {
      // Fallback para copiacola se não houver qrcode
      onShowQRCode?.(vendaFinal.copiacola || "", undefined);
    }
  };

  return (
    <div className="border border-gray-200 dark:border-gray-600 rounded-lg p-4 hover:shadow-md transition-shadow">
      <div className="flex items-center justify-between mb-3">
        <div className="flex items-center space-x-3">
          {getStatusIcon(vendaFinal.status)}
          <div>
            <p className="font-medium text-gray-900 dark:text-white">
              Fatura #{vendaFinal.id}
            </p>
            <p className="text-sm text-gray-600 dark:text-gray-400">
              {vendaFinal.type} - Vencimento: {formatDate(vendaFinal.vencimento as any)}
            </p>
          </div>
        </div>
        <div className="flex items-center space-x-2">
          <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(vendaFinal.status)}`}>
            {statusTranslate(vendaFinal.status)}
          </span>
          <span className="text-lg font-bold text-gray-900 dark:text-white">
            {formatCurrency(vendaFinal.valor)}
          </span>
        </div>
      </div>
      
      {/* Botão de Gerar Fatura - quando não há instruções de pagamento */}
      {(!vendaFinal.copiacola && !vendaFinal.codigobarra) && (
        <div className="mt-3 flex justify-center">
          <GenerateFaturaButton
            vendaFinalId={vendaFinal.id}
            status={vendaFinal.status}
            id_payment={vendaFinal.id_payment}
            onSuccess={() => {
              toast.success('Fatura gerada com sucesso!');
              onGenerateSuccess?.();
            }}
            onError={(error) => {
              toast.error(error);
            }}
          />
        </div>
      )}
      
      {/* Instruções de Pagamento */}
      {vendaFinal.status === 'PENDING' && (
        <div className="mt-3 p-4 bg-gray-50 dark:bg-gray-700 rounded-lg">
          <h4 className="font-medium text-gray-900 dark:text-white mb-3">
            Instruções de Pagamento
          </h4>
          
          {vendaFinal.type === 'PIX' && vendaFinal.copiacola && (
            <div className="space-y-3">
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                  Código PIX (Copiar e Colar):
                </p>
                <div className="space-y-2">
                  <div className="flex items-center space-x-2">
                    <input
                      type="text"
                      value={vendaFinal.copiacola}
                      readOnly
                      className="flex-1 bg-white dark:bg-gray-600 text-xs font-mono text-gray-700 dark:text-gray-300 border border-gray-300 dark:border-gray-500 rounded px-3 py-2"
                      aria-label="Código PIX para copiar"
                    />
                    <button
                      onClick={() => copyToClipboard(vendaFinal.copiacola!, 'Código PIX')}
                      className={`px-3 py-2 rounded-lg transition-colors flex items-center space-x-1 whitespace-nowrap ${
                        copied 
                          ? 'bg-green-100 text-green-700 dark:bg-green-900 dark:text-green-300' 
                          : 'bg-blue-100 text-blue-700 dark:bg-blue-900 dark:text-blue-300 hover:bg-blue-200 dark:hover:bg-blue-800'
                      }`}
                      title="Copiar código PIX"
                    >
                      <CopyIcon className="w-5 h-5" />
                      <span className="text-xs font-medium">
                        {copied ? 'Copiado!' : 'Copiar'}
                      </span>
                    </button>
                  </div>
                  <div className="flex justify-center">
                    <button
                      onClick={showQRCode}
                      className="px-4 py-2 bg-gray-100 text-gray-700 dark:bg-gray-600 dark:text-gray-300 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-500 transition-colors flex items-center space-x-2"
                      title="Mostrar QR Code"
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v1m6 11h2m-6 0h-2v4m0-11v3m0 0h.01M12 12h4.01M16 20h4M4 12h4m12 0h.01M5 8h2a1 1 0 001-1V5a1 1 0 00-1-1H5a1 1 0 00-1 1v2a1 1 0 001 1zm12 0h2a1 1 0 001-1V5a1 1 0 00-1-1h-2a1 1 0 00-1 1v2a1 1 0 001 1zM5 20h2a1 1 0 001-1v-2a1 1 0 00-1-1H5a1 1 0 00-1 1v2a1 1 0 001 1z" />
                      </svg>
                      <span className="text-sm font-medium">Ver QR Code</span>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}
          
          {vendaFinal.type === 'BOLETO' && vendaFinal.codigobarra && (
            <div className="space-y-3">
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                  Código de Barras:
                </p>
                <div className="flex items-center space-x-2">
                  <input
                    type="text"
                    value={vendaFinal.codigobarra}
                    readOnly
                    className="flex-1 bg-white dark:bg-gray-600 text-sm font-mono text-gray-700 dark:text-gray-300 border border-gray-300 dark:border-gray-500 rounded px-3 py-2"
                    aria-label="Código de barras para copiar"
                  />
                  <button
                    onClick={() => copyToClipboard(vendaFinal.codigobarra!, 'Código de Barras')}
                    className={`px-3 py-2 rounded-lg transition-colors flex items-center space-x-1 ${
                      copied 
                        ? 'bg-green-100 text-green-700 dark:bg-green-900 dark:text-green-300' 
                        : 'bg-blue-100 text-blue-700 dark:bg-blue-900 dark:text-blue-300 hover:bg-blue-200 dark:hover:bg-blue-800'
                    }`}
                    title="Copiar código de barras"
                  >
                    <CopyIcon className="w-5 h-5" />
                    <span className="text-xs font-medium">
                      {copied ? 'Copiado!' : 'Copiar'}
                    </span>
                  </button>
                </div>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default FaturaCard;
