import React from 'react';
import { Modal } from '@/components/ui/modal';
import { CopyIcon } from '@/icons';
import toast from 'react-hot-toast';
import Image from 'next/image';

interface QRCodeModalProps {
  isOpen: boolean;
  onClose: () => void;
  pixCode?: string;
  qrCode?: string;
}

const QRCodeModal: React.FC<QRCodeModalProps> = ({
  isOpen,
  onClose,
  pixCode,
  qrCode
}) => {
  const copyToClipboard = async () => {
    if (!pixCode) return;
    try {
      await navigator.clipboard.writeText(pixCode);
      toast.success('Código PIX copiado para a área de transferência!');
    } catch (error) {
      toast.error('Erro ao copiar para a área de transferência');
    }
  };

  // Verificar se é um QR code base64 ou código PIX
  const isBase64QRCode = qrCode?.startsWith('data:image') || qrCode?.startsWith('/9j/') || qrCode?.startsWith('iVBORw0KGgo');

  return (
    <Modal 
      isOpen={isOpen} 
      onClose={onClose} 
      title="QR Code PIX"
      className="max-w-[600px] m-4"
    >
      <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
        <div className="px-2 pr-14">
          <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
            QR Code PIX
          </h4>
          <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
            Escaneie o QR Code abaixo com seu aplicativo de pagamento ou copie o código PIX
          </p>
        </div>
        
        <div className="space-y-6">
          <div className="text-center">
            {/* QR Code */}
            <div className="bg-white p-8 rounded-lg border-2 border-dashed border-gray-300 dark:border-gray-600 mb-6">
              {isBase64QRCode ? (
                <div className="w-64 h-64 mx-auto">
                  <Image 
                    src={qrCode?.startsWith('data:image') ? qrCode : `data:image/png;base64,${qrCode}`} 
                    alt="QR Code PIX" 
                    className="w-full h-full object-contain rounded-lg"
                    width={256}
                    height={256}
                  />
                </div>
              ) : (
                <div className="w-64 h-64 mx-auto bg-gray-100 dark:bg-gray-700 rounded-lg flex items-center justify-center">
                  <div className="text-center">
                    <svg className="w-16 h-16 text-gray-400 mx-auto mb-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v1m6 11h2m-6 0h-2v4m0-11v3m0 0h.01M12 12h4.01M16 20h4M4 12h4m12 0h.01M5 8h2a1 1 0 001-1V5a1 1 0 00-1-1H5a1 1 0 00-1 1v2a1 1 0 001 1zm12 0h2a1 1 0 001-1V5a1 1 0 00-1-1h-2a1 1 0 00-1 1v2a1 1 0 001 1zM5 20h2a1 1 0 001-1v-2a1 1 0 00-1-1H5a1 1 0 00-1 1v2a1 1 0 001 1z" />
                    </svg>
                    <p className="text-xs text-gray-500 dark:text-gray-400">QR Code</p>
                    <p className="text-xs text-gray-500 dark:text-gray-400">PIX</p>
                  </div>
                </div>
              )}
            </div>
            
            {/* Código PIX */}
            <p className="text-sm text-gray-600 dark:text-gray-400 mb-4">
              Ou copie e cole o código PIX abaixo:
            </p>
            
            <div className="flex items-center space-x-2 mb-6">
              <input
                type="text"
                value={pixCode}
                readOnly
                className="flex-1 bg-gray-50 dark:bg-gray-700 text-xs font-mono text-gray-700 dark:text-gray-300 border border-gray-300 dark:border-gray-500 rounded px-3 py-2"
                aria-label="Código PIX para copiar"
              />
              <button
                onClick={copyToClipboard}
                className={`px-3 py-2 rounded-lg transition-colors flex items-center space-x-1 ${
                  'bg-blue-100 text-blue-700 dark:bg-blue-900 dark:text-blue-300 hover:bg-blue-200 dark:hover:bg-blue-800'
                }`}
                title={'Copiar código PIX'}
              >
                <CopyIcon className="w-5 h-5" />
                <span className="text-xs font-medium">Copiar</span>
              </button>
            </div>
          </div>
          
          <div className="flex justify-end">
            <button
              onClick={onClose}
              className="px-4 py-2 bg-gray-100 text-gray-700 dark:bg-gray-700 dark:text-gray-300 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-600 transition-colors"
            >
              Fechar
            </button>
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default QRCodeModal;
